*{
    padding:0;
    box-sizing: border-box;
    margin: 0;
    overflow-x: hidden;
    font-family: "Poppins", sans-serif;
}
body{
    width:100vw;
    position: relative;
}
.flex{
    display: flex;
}
.ConsumerLoginMain{
    width:100%;
    height:100vh;
    position: relative;
    justify-content:flex-end;
    align-items: start;
    flex-direction: column;
    padding:10px;
}
.ConsumerLoginMain .img{
    position: relative;
    width:100%;
    max-height: calc(100vh - 400px);
    overflow: hidden;
    margin:40px 0 20px 0;
}
.ConsumerLoginMain .img img{
    height:100%;
    width:100%;
    object-fit: contain;
}
.ConsumerLoginMain p{
    font-family: "Playpen Sans Deva", cursive;
    font-size:18px;
    margin-bottom: 15px;
}
.ConsumerLoginMain span{
    font-size:13px;
}
.ConsumerLoginMain input{
    margin:10px 0;
    font-size: 15px;
    font-family: "Playpen Sans Deva", cursive;
    line-height:15px;
    padding:5px 15px;
    outline: none;
    border:none;
    border:1px solid grey;
    border-radius:5px;
}
.ConsumerLoginMain button{
    width:100%;
    max-width:250px;
    margin: 10px auto;
    cursor: pointer;
    background-color: #c08312;
    color: white;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size:15px;
    padding:10px 0;
}